import './App.css';
import React, {useState} from 'react';

class Item extends React.Component {
  setAmountOf (e) {
    this.setState({amountOf: e.target.value});
    this.props.updateItemList({id:this.state.id,amount:e.target.value * this.state.price})
  }
  constructor(props){
    super(props);
    this.state = {
      title:this.props.title,
      price:this.props.price,
      amountOf:0,
      id:this.props.uid
    }
    this.setAmountOf = this.setAmountOf.bind(this);
  }

  render () {
    return (
    <div className="item">
      <span class="title">{this.props.title} | ${this.state.price}</span>
      <img src={this.props.img} alt="foo"/>
      <input onInput={this.setAmountOf} type="number" min="0" max="100" value={this.state.amountOf}/>
      <span>{this.state.amountOf}</span>
    </div>
    )
  }
}

var items = [
  {
    id:0,
    price:16,
    title:"Pan",
    img:"pan"
  },
  {
    id:1,
    price:4,
    title:"Hat",
    img:"hat"
  },
  {
    id:2,
    price:13,
    title:"tulip",
    img:"tulip"
  },
  {
    id:3,
    price:110,
    title:"game controller",
    img:"gc"
  }
];

function getItems(updateFunc) {
  return items.map((e)=>{
    return <Item uid={e.id} img={"imgs/"+e.img+".jpg"} title={e.title} price={e.price} updateItemList={updateFunc}/>
  })
}

class DiscountCode extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      code:"shop20",
      value:"",
      discounts:0
    };
    this.addDiscount = this.addDiscount.bind(this);
  }
  addDiscount(){
    let old_value = this.state.value;
    this.setState({value:""})
    if(old_value == this.state.code){
      this.props.updateFunc(20);
    } else {
      alert("invalid code");
    }
  }
  render () {
    return (
    <div>
      <input value={this.state.value} onChange={(e)=>{this.setState({value:e.target.value})}}/>
      <button onClick={this.addDiscount}>add discount</button>
      <div style={{display:(this.state.discounts == 0)? 'none' : 'block'}}>discount applied: {this.state.discounts}%</div>
    </div>
    )
  }
}

class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      initBalance: 100,
      item_list: {},
      total:0,
      discount:0,
      subtotal:0,
      balance:100,
      points:0
    }
    this.updateItemList = this.updateItemList.bind(this);
    this.addDiscount = this.addDiscount.bind(this);
    this.Pay = this.Pay.bind(this);
  }

  setSubtotal() {
    let total = this.state.total;
    let discount = this.state.discount;
    console.log(total,discount)
    this.setState({subtotal:total - ((discount / 100)*total)})
  }

  Pay () {
    let total = this.state.total;
    let balance = this.state.balance;
    this.setState({
      discount:0,
      balance:balance - this.state.subtotal,
      points:total
    },this.setSubtotal)
  }

  setTotal() {
    let total = 0;
    for(let id in this.state.item_list){
      total += this.state.item_list[id];
    }
    this.setState({
      total:total
    },()=>{
      this.setSubtotal()
    })
  }

  addDiscount (value) {
    this.setState({
      discount:this.state.discount + value
    },()=>{
      this.setSubtotal()
    })
  }

  updateItemList({id,amount}) {
    let il = this.state.item_list;
    il[id] = amount;
    this.setState({item_list:il})
    this.setTotal();
    console.log(this.state.total)
    console.log(il)
  }

  render() {
    return (
      <div className="App">
        <header>
          <h1>Balance: ${this.state.balance}</h1>
          
        </header>
        <main>
          <div>

          </div>
          <div class='items'>{getItems(this.updateItemList)}</div>
          <h3>Subtotal: ${this.state.total}</h3>
          <DiscountCode updateFunc={this.addDiscount}/>
          <h3>Discount: {this.state.discount}%</h3>
          <h2>Total: ${this.state.subtotal}</h2>
          <button onClick={this.Pay} disabled={this.state.subtotal > this.state.balance}>PAY</button>
          <div>
            You've paid for items amount to: ${this.state.points} in value
          </div>
        </main>
        <footer>
  
        </footer>
      </div>
    );
  }
}

export default App;
